@import url(//fonts.googleapis.com/css?family=Droid+Sans);
.sidebar {
  width: 2.65in;
  text-align: left;
  height: 100%;
  color: #fff;
  display: inline-block;
  left: 0;
  position: absolute;
}

.sidebar-photo {
  display: block;
  border: medium solid white;
  border-radius: 50%;
  margin: auto;
  margin-top: 1rem;
  transition: border-radius 100ms ease-in-out;
}

.sidebar-photo:hover {
  border-radius: 0;
}

.sidebar-heading {
  padding: 0.25rem 1rem;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  font-family: 'Droid Sans';
  text-align: center;
  font-weight: 300;
  font-size: 26pt;
}

.sidebar-subHeading {
  display: block;
  text-align: center;
  line-height: 1.2rem;
}

.sidebar-sectionHeader {
  padding: 0.25rem 1rem;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
}

.sidebar-sectionContents {
  padding: 0 1rem;
  font-size: 11pt;
  margin: 0.5rem 0;
  line-height: 1.2rem;
}

.sidebar-link {
  color: #fff;
}

.main {
  position: absolute;
  display: inline-block;
  width: 5.85in;
  right: 0;
}

.main-intro {
  font-size: 11pt;
  padding: 1.5rem 1rem;
  font-weight: 500;
}

.main-intro--nineties {
  padding: 0.5rem 1rem;
}

.main-sectionHeader {
  color: #0A3154;
  text-align: left;
  margin: 0 1.5rem 0 0.5rem;
  padding: 0.5rem 0.5rem;
  border-bottom: 0.1rem solid #0A3154;
  border-top: 0.1rem solid #0A3154;
  font-family: 'Droid Sans';
  font-weight: 700;
}

.main-sectionContainer {
  padding: 1rem 0;
}

.language-name {
  font-size: 11pt;
}

.language-proficiencyContainer {
}

.language-proficiency {
  text-align: right;
  font-size: 8pt;
}

.language-proficiencyBar {
  background-color: #0f0;
  height: 0.5rem;
  margin: 0.5rem 0 0 0;
}

.language-proficiencyBar--strong {
  background-color: #0f0;
}

.language-proficiencyBar--moderate {
  background-color: #ff0;
}

.language-proficiencyBar--some {
  background-color: #f80;
}

.language-proficiencyBar--novice {
  background-color: #f88;
}

.language-proficiencyBar--none {
  background-color: #000;
}

.experience {
  display: -webkit-flex;
  display: flex;
  padding: 0.5rem 0.75rem;
  text-align: left;
  font-size: 11pt;
}

.experience-dates {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.experience-details {
  -webkit-flex: 4 1;
          flex: 4 1;
}

.experience-titleRow {

}

.experience-title {
  display: inline-block;
  font-size: 12pt;
  font-weight: 700;
}

.experience-projectLink {
  position: absolute;
  right: 2rem;
}

.experience-location {
  font-style: italic;
  padding-bottom: 0.25rem;
}

.experience-points {
  -webkit-padding-start: 1rem;
          padding-inline-start: 1rem;
  margin-bottom: 0rem;
}

.experience-point {
  padding-left: 1rem;
}

.education {
  padding: 0.5rem 0.75rem;
  text-align: left;
  font-size: 11pt;
}

.education-title {
  font-size: 12pt;
  font-weight: 700;
}

.education-location {
  font-style: italic;
  padding-bottom: 0.25rem;
}

.education-points {
  -webkit-padding-start: 1rem;
          padding-inline-start: 1rem;
  margin-bottom: 0rem;
}

.education-point {
  padding-left: 1rem;
}

.modal-overlay {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.modal-container {
  background-color: white;
  outline: none;
  border-radius: 0.25rem;
  padding: 1rem;
  position: absolute;
  top: 48px;
  left: 128px;
  right: 128px;
  bottom: 48px;
}

.modal-title {
  display: inline-block;
  margin-top: 0;
}

.modal-close {
  color: #888;
  right: 1rem;
  position: absolute;
  display: inline-block;
}

.modal-projectContainer {
  overflow-y: scroll;
  height: calc(100% - 2rem);
}

.ReactModal__Overlay {
  opacity: 0;
  transition: 300ms ease-in-out;
}

.ReactModal__Overlay--after-open{
  opacity: 1;
}

.ReactModal__Overlay--before-close{
  opacity: 0;
}

.project {
  margin: 1rem 0;
  padding-top: 1rem;
  border-top: thin solid #888;
  width: calc(100% - 1rem);
}

.project-name {
  margin: 0;
}

.project-imagesContainer {
  margin-top: 1rem;
  overflow-x: auto;
  white-space: nowrap;
}

.project-imageContainer {
  height: 256px;
  display: inline-block;
  position: relative;
  left: 1rem;
  transition: 100ms ease-in-out;
  z-index: 10;
  margin: 0 0.5rem;
}

/*
.project-imageContainer:focus {
  height: 512px;
  display: inline-block;
  position: absolute;
  left: 1rem;
  z-index: 11;
  margin: 0;
}
*/

.project-image {
  height: 100%;
}

.randomFact {
  display: block;
  position: relative;
  top: 1.5rem;
  margin: auto;
  color: white;
}

.randomFact-marquee {
  width: 7in;
  margin-right: 1rem;
}

.randomFact > button {
  color: white;
  margin-left: 1rem;
  background-color: #888;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  padding: 2px 0 0 0;
}

.randomFact > button:hover {
  border: thin solid white;
}

.randomFact > button:focus {
  outline: none;
}



