    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  background-color: #555;
  text-align: center;
  padding-bottom: 2rem;
  height: 100%;
  width: 100%;
}

