  cursor: url(/static/media/cur45.2f818555.ani), url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAACAAAAAgCAYAAABzenr0AAAAd0lEQVRYw+2WQQ7AIAgE+f+np5cmWrX05K5pWLIHT0xAwYgIbtsEZggwQwDNDogHgAOCZQgheA0RBHMTOgsgcgABBN1LTLwP4ivp6B2DqNW7P8su4ZDQATCVVzqeV0nkAOmIti8p66p27IajAOKEL1upVCqV/q0LPfzoGN6p5AcAAAAASUVORK5CYII=), default !important;
