.resume {
  width: 8.5in;
  height: 11in;
  background-color: #ffffff;
  display: inline-block;
  margin: 3.75rem auto 0 auto;
  position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.4), 0 6px 20px 0 rgba(0, 0, 0, 0.4)
}

.downloadLink {
  display: block;
  position: relative;
  font-size: 14pt;
  margin-bottom: 2rem;
}

.button-clear {
  color: -webkit-link;
  cursor: pointer;
  -webkit-appearance: none;
  background: none;
  border: none;
}

.styleContainer {
  width: 8.5in;
  margin: 0 auto;
  text-align: right;
}

.styleButtonGroup {
  display: inline-block;
  padding-top: 1rem;
}

.styleButton {
  border-radius: 1rem;
  background-color: white;
  width: 1rem;
  height: 1rem;
  padding: 0.8rem;
  overflow: hidden;
  transition: 200ms ease-in-out;
  margin-left: 0.5rem;
  color: black;
}

.styleButton:hover {
  width: 12rem;
}

.styleButtonIcon {
  display: block;
  margin-top: -0.35rem;
  margin-left: -0.35rem;
}

.visitorCounter {
  color: #ff0088;
  font-size: 16pt;
  margin: 1rem 0;
}

.visitorCounter-number {
  margin-top: 0.5rem;
  color: #00ff00;
}

